<script>
import { mapState, mapGetters } from "vuex";

export default {
  data: () => ({
    offset: true,
    ractive: false,
    active: false,
    name: JSON.parse(localStorage.getItem("in:user")).clientData.displayName,
  }),
  computed: {
    ...mapState("auth", ["userProfile"]),
    ...mapGetters("auth", ["isAdmin"]),
  },
  methods: {
    signOut() {
      localStorage.clear();
      this.$router.push({
        name: "Login",
      });
    },
    goTo(path, params) {
      this.$router.push({
        path,
        params,
      });
    },
    formatName(name) {
      const nameVal = name.toLowerCase();
      const names = nameVal.split(" ");
      console.log("names : ", names[0].substring(0, 1));
      return names
        .map((n) => n.substring(0, 1).toUpperCase() + n.substring(1))
        .join(" ");
    },
  },
};
</script>
