  computed: {
    getUserDetails() {
      return this.$store.getters["auth/loggedInDetails"];
    },
    rows() {
      return this.tableData.length;
    },
  },
  async mounted() {
    setAuthorizationHeader("ZGVhbGVyOmRlYWxlcg==");
    this.myFunction();
    console.log("mounted");
  },
