<script>
import Button from "@/components/Elements/Button.vue";
import { mapState, mapGetters } from "vuex";

export default {
  components: {
    Button,
  },
  data() {
    return {
      Balance: JSON.parse(localStorage.getItem("in:user")).clientBalanceData
        .balanceAmount,
      clients: [],
      amount: 0,
    };
  },
  mounted() {
    var clientdata = JSON.parse(localStorage.getItem("in:user"))
      .clientOrdersData;
    console.log(this.amount);
      this.amount = 0;
    clientdata.map((e) => {
      if (e.status === "ACTIVE") {
        this.amount = this.amount + e.price;
      }
    });
  },
  computed: {
    ...mapState("auth", ["walletBalance"]),
    ...mapGetters("auth", ["monthlyCost", "basePack", "isDP"]),
    basePackExpired() {
      return this.basePack && this.basePack.expired === true;
    },
  },
  methods: {
    goTo(path, params) {
      this.$router.push({
        path,
        params,
      });
    },
  },
};
