<template>
  <Layout>
    <!-- end row -->
    <div class="account-pages my-5">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-xl-10">
            <b-card
              class="card-wrap"
              header="Forgot Password"
              border-variant="info"
              header-text-variant="white"
              style="width:600px;margin-left:100px"
            >
              <div class="card-body p-0">
                <div class="row">
                  <div class="col-md-12 p-12">
                    <div class="mx-auto mb-5">
                      <!-- <a routerLink="/">
                        <img src="@assets/images/tstvlogo.png" alt height="38" />
                        <h5 class="d-inline align-middle ml-1 text-logo">Forgot your password</h5>
                      </a>-->
                    </div>
                    <b-form class="authentication-form">
                      <div class="form-group">
                        <label class="form-control-label">Username</label>
                        <div class="input-group input-group-merge">
                          <div class="input-group-prepend"></div>
                          <b-form-input
                            id="input-1"
                            v-model="username"
                            type="text"
                            required
                            placeholder="Enter username"
                          ></b-form-input>
                          <b-form-group id="button-group" class="mt-0" style="margin-left:5px">
                            <b-button
                              style="background: linear-gradient(40deg, #45cafc, #303f9f);"
                              type="submit"
                              variant="primary"
                              class="btn-block"
                              @click.prevent="myFunction"
                            >Submit</b-button>
                          </b-form-group>
                        </div>
                        <b-form-input
                          id="input-1"
                          v-model="confirmemail"
                          type="text"
                          required
                          placeholder="Check Email Id"
                          disabled
                        ></b-form-input>
                      </div>
                      <b-alert v-model="isAuthError" variant="success" dismissible>{{message}}</b-alert>
                      <b-form-group
                        id="button-group"
                        class="mt-0"
                        style="margin-left:5px;margin-top:5px"
                      >
                        <b-button
                          style=" background: linear-gradient(40deg, #45cafc, #303f9f);"
                          type="submit"
                          variant="primary"
                          class="btn-block"
                          @click.prevent="email()"
                        >Confirm Email</b-button>
                      </b-form-group>
                      <div class="col-md-12 text-right">
                        <div>
                          <b
                            style="cursor: pointer;"
                            @click="$router.push('/forgot-username')"
                          >Forgot Username?</b>
                        </div>
                      </div>
                    </b-form>
                  </div>
                </div>
              </div>
            </b-card>
            <!-- end card-body -->
          </div>
          <!-- end row -->
        </div>
        <!-- end col -->
      </div>
      <!-- end row -->

      <!-- end container -->
    </div>
  </Layout>
</template>
<script>
// import appConfig from '@src/app.config'
// import Multiselect from 'vue-multiselect'
// import moment from 'moment'
// import NProgress from 'nprogress/nprogress'
// import Rave from 'vue-ravepayment'
import { setAuthorizationHeader } from "../../services/index"
// import backgroundUrl from '@assets/images/header-bg.jpg'
import { verifyusername, confirmemail } from "../../services/register"

export default {
 
  data() {
    return {
      username: '',
      useremail: null,
      confirmemail: null,
      message: null,
      // message:null,
      // isAuthError:false,
      isAuthError: false,

      clientId: null,
    }
  },
  computed: {
    getUserDetails() {
      return this.$store.getters['auth/loggedInDetails']
    },
  },
  async mounted() {
    setAuthorizationHeader('ZGVhbGVyOmRlYWxlcg==')
    // console.log('mounted', this.username)
  },
  methods: {
    async protect_email(confirmemail) {
      var avg, splitted, part1, part2
      splitted = confirmemail.split('@')
      part1 = splitted[0]
      avg = part1.length / 2
      part1 = part1.substring(0, part1.length - avg)
      part2 = splitted[1]
      return part1 + '***@' + part2
      // console.log('email', protect_email)
    },
    async myFunction() {
      try {
        setAuthorizationHeader('ZGVhbGVyOmRlYWxlcg==')
       
        const result = await verifyusername(this.username)
        this.confirmemail = await this.protect_email(result.data.email)
        this.useremail = result.data.email
        // console.log("mailid",this.confirmemail)
      } catch (e) {
        this.$toasted.error(e.message.developerMessage, {
          duration: 7000,
        })

      
      }
    },
    async email() {
       try {
        const result = await confirmemail(this.useremail)
        if (result) {
            this.isAuthError = true
          //    this.form = {}
          // this.query = "";
          this.$swal({
           title: "Congratulation",
            type: 'success',
            text: ` Email Sent  Successfully`,
            duration: 5000,
          })
             (this.message = `Your request sent to your email please check and change password`);
        }
      } catch (e) {
       
        this.$toasted.error(e.message.errors[0].developerMessage,{
          duration: 7000
        });
       
      }
    },
  },
}
</script>
