<template>
  <Layout>
    <!-- end row -->
    <div class="account-pages my-5">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-xl-10">
            <b-card
              class="card-wrap"
              header="Forgot Password"
              border-variant="info"
              header-text-variant="white"
              style="width:600px;margin-left:100px"
            >
              <div class="card-body p-0">
                <div class="row">
                  <div class="col-md-12 p-12">
                    <div class="mx-auto mb-5">
                      <!-- <a routerLink="/">
                        <img src="@assets/images/tstvlogo.png" alt height="38" />
                        <h5 class="d-inline align-middle ml-1 text-logo">Forget your username</h5>
                      </a>-->
                    </div>
                    <b-form class="authentication-form">
                      <div class="form-group">
                        <label class="form-control-label">Enter Email Id</label>
                        <div class="input-group input-group-merge">
                          <div class="input-group-prepend"></div>
                          <b-form-input
                            id="input-1"
                            v-model="email"
                            type="email"
                            required
                            placeholder="Enter Email Id"
                          ></b-form-input>
                          <b-form-group id="button-group" class="mt-0" style="margin-left:5px">
                            <b-button
                            style=" background: linear-gradient(40deg, #45cafc, #303f9f);"
                              type="submit"
                              variant="primary"
                              class="btn-block"
                              @click.prevent="myFunction"
                            >Submit</b-button>
                          </b-form-group>
                        </div>

                        <b-form-input
                          id="input-1"
                          v-model="emailid"
                          type="text"
                          required
                          disabled
                          placeholder="Check Email Id"
                        ></b-form-input>
                      </div>
                      <b-alert v-model="isAuthError" variant="success" dismissible>{{message}}</b-alert>
                      <b-form-group
                        id="button-group"
                        class="mt-0"
                        style="margin-left:5px;margin-top:5px"
                      >
                        <b-button
                        style=" background: linear-gradient(40deg, #45cafc, #303f9f);"
                          type="submit"
                          variant="primary"
                          class="btn-block"
                          @click.prevent="emailconfirm"
                        >Confirm Email</b-button>
                      </b-form-group>
                    </b-form>
                  </div>
                  <!-- <div class="col-lg-6 d-none d-md-inline-block">
                    <div class="auth-page-sidebar">
                      <div class="overlay"></div>
                      <div class="auth-user-testimonial">
                        <p class="font-size-24 font-weight-bold text-white mb-1">I simply love it!</p>
                        <p class="lead">"It's a elegent templete. I love it very much!"</p>
                        <p>- Admin User</p>
                      </div>
                    </div>
                  </div>-->
                </div>
              </div>
            </b-card>
            <!-- end card-body -->
          </div>
          <!-- end row -->
        </div>
        <!-- end col -->
      </div>
      <!-- end row -->

      <!-- end container -->
    </div>
  </Layout>
</template>
<script>
import { setAuthorizationHeader } from "../../services/index"
import { verifyemail, confirmemail } from  "../../services/register"

export default {
  
  data() {
    return {
      email: '',
      useremail: '',
      emailid: null,
      confirmemail: null,
      message: null,
      // message:null,
      // isAuthError:false,
      isAuthError: false,
      clientId: null,
    }
  },
  computed: {
    getUserDetails() {
      return this.$store.getters['auth/loggedInDetails']
    },
    rows() {
      return this.tableData.length
    },
  },
  async mounted() {
    setAuthorizationHeader('ZGVhbGVyOmRlYWxlcg==')

    // console.log('mounted', this.email)
  },
  methods: {
    async protect_email(confirmemail) {
      var avg, splitted, part1, part2
      splitted = confirmemail.split('@')
      part1 = splitted[0]
      avg = part1.length / 2
      part1 = part1.substring(0, part1.length - avg)
      part2 = splitted[1]
      return part1 + '***@' + part2
      // console.log('email', protect_email)
    },
       async myFunction() {
      try {
        setAuthorizationHeader('aW50ZWdyYXRpb246aW50ZWdyYXRpb25AMjAyMA==')
     
        const result = await verifyemail(this.email)
        this.emailid = await this.protect_email(result.data.email)
        this.useremail = result.data.email
      } catch (e) {
       this.$toasted.error("something went wrong",{
          duration: 7000
        });
     
      }
    },

    async emailconfirm() {
      try {
        //    setAuthorizationHeader('ZGVhbGVyOmRlYWxlcg==')
        //   NProgress.start()
        const result = await confirmemail(this.useremail)
        if (result) {
            this.isAuthError = true
          //    this.form = {}
          // this.query = "";
          this.$swal({
            title: "Congratulation",
            type: 'success',
            text: `Email Sent Successfully`,
            duration: 5000,
          })
            (this.message = `Your request sent to your email please check and change password`);
        }
      } catch (e) {
      
      this.$toasted.error(e.message,{
          duration: 7000
        });
      }
    },
  },
}
</script>
<style lang="scss">
.card-header {
  padding: 0.75rem 1.25rem;
  margin-bottom: 0;
 background: linear-gradient(40deg, #45cafc, #303f9f);
  border-bottom: 0 solid rgba(0, 0, 0, 0.125);
}
</style>
